$play-online-border-color: #363636;

.prev-moves {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: calc(100% - 350px);
    width: 100%;

    .prev-moves-container {
        background-color: rgb(0, 0, 0, 0.13);
        height: 80%;
        width: 85%;
        border-radius: 25px;
        overflow: hidden;
        border: 1px solid;
        border-color: $play-online-border-color;
        box-shadow: 3px 3px 6px rgb(0, 0, 0, 0.2);

        .prev-moves-header {
            font-size: large;
            display: flex;
            height: 50px;
            justify-content: center;
            align-items: center;
            background-color: #242424;
            border-bottom: 1px solid;
            border-color: $play-online-border-color;
        }

        .prev-moves-content {
            height: calc(50vh - 50px);
            width: 100%;
            overflow-x: hidden;
            overflow-y: auto;

            .move {
                list-style-type: decimal;

                .prev-move {
                    cursor: pointer;
                    color: white;
                    font-weight: 600;
                    border: 0px;
                    background-color: #282828f0;
                    border-radius: 5px;
                    text-align: left;
                    width: 80px;
                    margin-right: 10px;
                    box-shadow: 1px 1px 3px 0px rgb(0, 0, 0, 0.1);

                    &:hover {
                        background-color: #2c2c2cf0;
                    }
                }

                .current-move {
                    background-color: #303030f0;

                    &:hover {
                        background-color: #303030f0;
                    }
                }
            }
        }
    }

    .prev-moves-buttons-container {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 10%;

        .prev-moves-button {
            cursor: pointer;
            display: flex;
            position: relative;
            justify-content: center;
            align-items: center;
            height: 45px;
            width: 90px;
            background-color: #252525;
            border-radius: 5px;
            border: 1px solid;
            border-color: $play-online-border-color;
            box-shadow: 3px 3px 6px rgb(0, 0, 0, 0.2);

            &:hover {
                background-color: #272727;
            }
        }
    }
}
